// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

* {
    box-sizing: border-box;
}

body {
    font-family: $OpenSans;
}

input:focus, button:focus {
    outline: none;
}

.hide {
    display: none;
}

.wrapper {
    width: 1368px;
    margin: 0 auto;
    padding: 0 40px;
}

.row {
    display: flex;
}

a {
    transition: .2s;
}

.btn-menu {
    display: none;
}

.mobile-menu {
    position: fixed;
    z-index: 1000;
    top: 0;
    left: 0;
    width: 200px;
    height: 100vh;
    padding: 20px 0;
    margin: 0;
    background-color: #46cace;
    transform: translateX(-200px);
    transition: .3s;
    transition-timing-function: cubic-bezier(0.785, 0.135, 0.150, 0.860);
    nav {
        a {
            display: block;
            text-decoration: none;
            color: #fff;
            padding: 10px 15px;
        }
    }
}

.mobile-menu.active {
    transform: translateX(0);
}

.logo {
    a {
        width: 300px;
        height: 130px;
        text-decoration: none;
        font-size: 10px;
        padding: 10px 25px;
        display: block;

        text-align: center;
        background: $conic-gradient;
        border-bottom-left-radius: 25px;
        border-bottom-right-radius: 25px;
    }
    .slogan {
        text-align: left;
        text-transform: uppercase;
        color: #fff;
        margin-top: 5px;
        &::first-letter {
            font-size: 12px;
            line-height: 16px;
        }
    }
    .upper {
        font-size: 12px;
    }
    &.header-logo {
        position: relative;
        z-index: 1;
    }
}

.header {
    background-color: #fff;
}

.header .row {
    justify-content: space-between;
    max-height: 65px;
}

.header-menu a {
    display: inline-block;
    font-size: 14px;
    text-decoration: none;
    color: #000;
    padding: 0 7px;
    transition: none;
    span {
        display: block;
        padding: 20.5px 5px;
        border-top: 4px solid transparent;
        border-bottom: 4px solid transparent;
        transition: .2s;
    }
    &:hover, &.active {
        color: #46cace;
        span {
            border-top-color: #46cace;
            text-shadow: 0 0 2px #46cace;
        }
    }
}

.header-phone {
    display: flex;
    align-items: center;
    font-family: $BebasNeue;
    font-size: 24px;
    a {
        color: #51c73b;
        text-decoration: none;
        &:hover {
            color: #46cace;
        }
    }
}

.banner {
    background: url(../images/banner.jpg) no-repeat center;
    background-size: cover;
    padding: 200px 0;
    color: #fff;
    p {
        font-size: 16px;
        line-height: 30px;
        margin: 0 0 10px 0;
    }
    h1 {
        margin: 0 0 10px 0;
    }
    button {
        font-size: 14px;
        font-weight: 700;
        width: 320px;
        height: 64px;
        border: none;
        border-radius: 50px;
        background: rgba(255,255,255,.5);
        box-shadow: 6px 6px 22px rgba(0,0,0,.15);
        &:hover {
           
        }
    }
    .button-group {
        margin-top: 50px;
    }
}

.banner-text, .banner-quiz {
    width: 50%;
}

h1 {
    font-size: 73px;
    text-transform: uppercase;
    letter-spacing: 0;
    font-style: normal;
    font-family: $BebasNeueCyrillic;
    font-weight: normal;
}

.banner-text {
    padding: 0 30px;
}

.banner-quiz {
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.quiz-form {
    width: 480px;
    margin-right: 25px;
    background: #fff;
    padding: 30px 25px 10px 25px;
    border-radius: 25px;
    color: #000;
    position: relative;
    .row {
        margin-bottom: 10px;
    }
    select {
        width: 100%;
    }
    input[type=text], input[type=email] {
        width: 100%;
        border: none;
        background-color: #e8e8e8;
        border-radius: 4px;
        padding: 0 8px;
        font-size: 14px;
        line-height: 32px;
    }
    input[type=submit], input.next, input.back {
        width: 148px;
        height: 38px;
        border-radius: 25px;
        background: none;
        color: #46cace;
        border: 1px solid #46cace;
        cursor: pointer;
        &:hover {
            background: $conic-gradient;
            color: #fff;
            border-color: transparent;
        }
    }
    input.back {
        margin-right: 15px;
    }
}

.quiz-form__ribbon {
    position: absolute;
    top: -4px;
    right: 20px;
    width: 122px;
    height: 84px;
    padding: 15px;
    background-color: #46cace;
    color: #fff;
    font-size: 14px;
    line-height: 18px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    box-shadow: $shadow;
}

.quiz-form__counter {
    position: absolute;
    right: 25px;
    bottom: 15px;
    color: #1c1b1b;
    font-size: 14px;
}

.quiz-form__title {
    font-size: 30px;
    text-transform: uppercase;
    font-family: $BebasNeueCyrillic;
    font-weight: normal;
    margin-bottom: 25px;
}

.select2-container--default .select2-selection--single {
    border: none;
    background-color: #e8e8e8;
    outline: none !important;
    height: 32px;
    display: flex;
}

.select2-container--default .select2-selection--single .select2-selection__arrow {
    height: 32px;
    position: static;
    display: flex;
    justify-content: center;
    align-items: center;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
    margin: 0;
    padding: 0;
    position: static;
}

.select2-dropdown {
    border: none;
}

.select2-container {
    font-size: 14px;
    min-width: 100%;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
    color: #1c1b1b;
    line-height: 32px;
    padding-right: 0;
}

.select2-container--default .select2-results__option--highlighted.select2-results__option--selectable {
    background-color: #46cace;
}

.office,
.shop {
    padding: 100px 0;
    position: relative;
    z-index: 1;
}

.office {
    &:after {
        content: url(../images/notebook.png);
        position: absolute;
        right: 0;
        top: 60%;
        z-index: -1;
    }
}

.title {
    h2 {
        font-size: 48px;
        text-transform: uppercase;
        font-family: $BebasNeueCyrillic;
        font-weight: normal;
        margin: 0;
    }
    ~ p {
        font-size: 16px;
        line-height: 30px;
        margin: 0;
    }
}

.items {
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.item {
    width: 567px;
    display: flex;
    padding: 0 40px;
    background-color: #fff;
    box-shadow: $shadow;
    margin-bottom: 16px;
    border-radius: 20px;
    font-size: 16px;
    line-height: 36px;
}

.office-item__name,
.shop-item__name {
    flex: 1;
}

.office-item__price,
.shop-item__price {
    width: 50px;
}

.office-item__count,
.shop-item__count {
    width: 50px;
}

.office-wrapper .button-group,
.shop-wrapper .button-group {
    text-align: center;
    margin-top: 60px;
    button {
        width: 340px;
        height: 64px;
        border: 1px solid transparent;
        color: #fff;
        font-size: 14px;
        font-weight: 700;
        border-radius: 50px;
        background: $conic-gradient;
        &:hover {
            background: none;
            color: #46cace;
            border: 1px solid #46cace;
        }
    }
}

.signage {
    background: url(../images/singage.jpg) no-repeat center;
    background-size: cover;
    padding: 80px 0;
}

.signage-text {
    width: 730px;
    border-radius: 25px;
    padding: 40px 35px;
    background-color: rgba(255,255,255,.6);
    box-shadow: $shadow;
    h2 {
        margin-bottom: 10px;
    }
    h3 {
        font-size: 16px;
        text-transform: uppercase;
        margin-top: 20px;
    }
    p {
        margin-bottom: 10px;
    }
    ul {
        padding: 0;
        list-style: none;
        font-size: 16px;
        li {
            position: relative;
            padding-left: 10px;
            margin: 10px 0;
            &:before {
                content: '';
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background: #000;
                position: absolute;
                left: 0;
                top: calc(50% - 2px);
            }
        }
    }
}

.contact {
    background: $conic-gradient;
    color: #fff;
    .text h2 {
        text-transform: uppercase;
        font-family: $BebasNeueCyrillic;
        font-weight: normal;
        font-size: 60px;
        padding-left: 60px;
        span {
            font-size: 24px
        }
    }
    .button-group {
        display: flex;
        flex: 1;
        justify-content: center;
        align-items: center;
        button {
            font-size: 14px;
            font-weight: 700;
            width: 320px;
            height: 64px;
            border: none;
            border-radius: 50px;
            background: rgba(255, 255, 255, 0.5);
            box-shadow: 6px 6px 22px rgba(0, 0, 0, 0.15);
        }
        a {
            height: 64px;
            width: 64px;
            border-radius: 50%;
            margin: 0 30px;
            padding: 15px;
            background: rgba(255, 255, 255, 0.5);
            box-shadow: 6px 6px 22px rgba(0, 0, 0, 0.15);
            .part2 {
                fill: #d3eaa1;
            }
        }
    }
}

.wifi {
    padding: 100px 0;
    .lSSlideOuter {
        margin-top: 70px;
    }
}



.wifi-slider {
    .slider-item {
        padding: 20px;
    }
    .wrap{
        width: 260px;
        height: 200px;
        margin: 0 auto;
        box-shadow: 6px 6px 22px rgba(0, 0, 0, 0.15);
        border-radius: 25px;
        padding: 20px 25px;
        display: flex;
        flex-direction: column;
        &:hover {
            background: $conic-gradient;
            color: #fff;
            .slider-item__link a {
                display: inline-block;
            }
        }
    }
    .slider-item__title {
        font-size: 24px;
        font-weight: normal;
        text-transform: uppercase;
        font-family: $BebasNeueCyrillic;
        margin-bottom: 10px;
    }
    .slider-item__text {
        font-size: 14px;
        line-height: 24px;
        margin-bottom: 10px;
        flex: 1;
    }
    .slider-item__link a {
        text-transform: uppercase;
        color: #fff;
        font-size: 14px;
        display: none;
        text-decoration: none;
        border-bottom: 1px solid #fff;
    }
}

.lSAction>a {
    background: none;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    &:hover {
        background-color: #46cace;
        color: #fff;
    }
}

.lSAction>.lSPrev {
    left: 0;
}

.lSAction>.lSNext {
    right: 0;
}


.storage {
    padding-bottom: 100px;
    h3 {
        text-transform: uppercase;
        margin: 50px 0 35px 0;
    }
}

.storage-item {
    width: 320px;
    height: 94px;
    margin-right: 65px;
    flex-direction: column;
    justify-content: center;
    padding: 20px 25px;
    .link {
        display: none;
        a {
            color: #fff;
            border-bottom: 1px solid #fff;
            text-transform: uppercase;
            text-decoration: none;
        }
    }
    &:hover {
        color: #fff;
        background: $conic-gradient;
        .link {
            display: block;
        }
    }
}

footer {
    padding: 75px 0;
    background-color: #5f5f5f;
    color: #fff;
    .row {
        justify-content: space-between;
    }
    .logo {
        margin-right: 130px;
    }
    .logo .social {
        display: flex;
        margin-top: 10px;
        a {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 10px;
            width: 41px;
            height: 41px;
            border-radius: 50%;
            background-color: #fff;
            color: #605f60;
            font-size: 21px;
            &:hover {
                background-color: #1c1b1b;
                color: #fff;
            }
        }
    }
    .text {
        font-size: 14px;
        line-height: 26px;
        p {
            margin: 0 0 15px 0;
        }
    }
    .contacts {
        min-width: 240px;
        margin-left: 120px;
        font-size: 14px;
        line-height: 30px;
        a {
            color: #fff;
            text-decoration: none;
            &:hover {
                color: #46cace;
            }
        }
    }
}

footer .logo a {
    background: none;
    width: auto;
    height: auto;
    padding: 0;
}

.under-footer {
    background-color: #444;
    color: #fff;
    padding: 6px 0;
    .row {
        justify-content: space-between;
        align-items: center;
    }
    .copyright {
        text-transform: uppercase;
        font-size: 12px;
    }
    .made a {
        font-size: 16px;
        color: #fff;
        text-decoration: none;
        &:hover {
            color: #46cace;
        }
    }
}


@media screen and (max-width: 1367px) {
    .wrapper {
        width: 100%;
        padding: 0 15px;
    }
    .row {
        flex-wrap: wrap;
    }
    footer .row {
        flex-wrap: nowrap;
    }
    .header-phone {
        padding-top: 20px;
    }
    .header .row {
        align-items: flex-start;
    }
}


@media screen and (max-width: 1250px) {
    .header-phone {
        display: none;
    }
    .btn-menu {
        display: block;
        margin-top: 5px;
    }
    .header-menu {
        display: none;
    }
}

@media screen and (max-width: 1200px) {
    .contact .text {
        width: 100%;
    }
    .contact .text h2 {
        padding: 0;
        text-align: center;
        margin: 20px 0;
    }
    .contact .button-group {
        margin-bottom: 20px;
    }
    .storage-item {
        margin: 0;
    } 
    .storage .row {
        justify-content: space-between;
    }
}

@media screen and (max-width: 1050px) {
    .banner-text, .banner-quiz {
        width: 100%;
        margin-bottom: 25px;
    }
    .banner {
        padding: 70px 0;
        padding-bottom: 30px;
    }
    .shop {
        padding-top: 0;
    }
    .office:after {
        top: 10%;
    }
    .storage-item {
        margin: 15px;
    } 
    .storage .row {
        justify-content: flex-start;
    }
    footer {
        padding: 15px 0;
    }
    footer .logo {
        margin-right: 0;
    }
    footer .contacts {
        margin-left: 0;
    }
    footer .text {
        margin: 0 20px;
    }
}

@media screen and (max-width: 768px) {
    .signage-text {
        width: 100%;
    }
    footer .row {
        flex-wrap: wrap;
    }
    footer .text {
        margin: 0;
    }
    .banner-text {
        padding: 0;
    }
    .quiz-form input[type=submit], .quiz-form input.next, .quiz-form input.back {
        margin-bottom: 10px;
    }
    h1 {
        font-size: 50px;
    }
    .wrapper h2,
    .contact .text h2 {
        font-size: 32px;
    }
    .signage-text {
        padding: 40px 10px;
    }
    .item {
        width: 100%;
        padding: 15px;
    }
    .signage {
        padding: 15px 0;
    }
    .shop, .office {
        padding: 30px 0;
    }
    .office-wrapper .button-group, .shop-wrapper .button-group {
        margin-top: 20px;
    }
    .items {
        margin-top: 20px;
    }
    .banner .button-group {
        margin-top: 20px;
    }
}


@media screen and (max-width: 560px) {
    .quiz-form  {
        width: 100%;
        margin: 0;
    }
    .office-item__name, .shop-item__name {
        margin-right: 25px;
        line-height: 26px;
    }
    .under-footer .copyright, .under-footer .made {
        width: 100%;
        text-align: center;
    }
    .under-footer .made {
        margin-bottom: 10px;
    }
}

@media screen and (max-width: 460px) {
    .contact .button-group {
        flex-wrap: wrap;
        button {
            margin-bottom: 20px;
        }
    }
}

@media screen and (max-width: 400px) {
    footer .contacts {
        margin: 0;
    }
    .btn-menu {
        width: 100%;
        text-align: right;
    }
}

@media screen and (max-width: 370px) {
    .office-wrapper .button-group button, .shop-wrapper .button-group button,
    .banner button,
    .contact .button-group button {
        width: 100%;
    }
}

@media screen and (max-width: 330px) {
    .logo a {
        width: 100%;
    }
}