// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$color-black: #000;
$color-white: #fff;

// BACKGROUNDS
$conic-gradient: conic-gradient(at 60% 30% , #a4d6ba, #afd743 25%, #4cc843 50%, #42c7ce 75%, #a4d6ba);

// FONTS
$BebasNeue: 'Bebas Neue', cursive;
$BebasNeueCyrillic: 'Bebas Neue Cyrillic';
$OpenSans: 'Open Sans', sans-serif;

// EASING TRANSITIONS
$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;

// BOX-SHADOWS

$shadow: 6px 6px 22px rgba(0,0,0,.15);