// stylelint-disable no-empty-source
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Open+Sans:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap');

@font-face {
    src: url("../fonts/BebasNeueCyrillic.woff2") format("woff2"), url("../fonts/BebasNeueCyrillic.woff") format("woff");
    font-family: "Bebas Neue Cyrillic";
    font-weight: 400;
    font-style: normal;
}

@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?cjw987');
  src:  url('../fonts/icomoon.eot?cjw987#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?cjw987') format('truetype'),
    url('../fonts/icomoon.woff?cjw987') format('woff'),
    url('../fonts/icomoon.svg?cjw987#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-chevron-right:before {
  content: "\e900";
}
.icon-chevron-left:before {
  content: "\e901";
}
.icon-facebook:before {
  content: "\e902";
}
.icon-vk:before {
  content: "\e903";
}
.icon-twitter:before {
  content: "\ea96";
}
  